body {
  margin: 0;
  padding: 0;
  background: url("https://i.imgur.com/osPV7yi.jpg");
  background-attachment: fixed;
  background-size: cover;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body:after {
  content: '';
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  background: rgba(255, 150, 150, 0.3); }

a {
  font-size: 20px;
  font-family: 'Product Sans Regular';
  font-weight: 200;
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none; }

section.content {
  margin: 5em 5em; }

span.breadcrumbs {
  font-size: 20px;
  font-family: 'Product Sans Regular';
  font-weight: 200;
  color: white;
  text-decoration: none;
  background: rgba(255, 255, 255, 0.2);
  padding: 5px;
  margin: 0 0 5em 0; }



